import React, { Component } from 'react';
import PreLoader, {apishka} from './Api'
import {NotificationContainer, NotificationManager} from 'react-notifications';
import List from './List'
import GetOne from './GetOne'
import Composition from './Composition'
import {PanelMenu} from 'primereact/panelmenu'
import {Tree} from 'primereact/tree';

const qs = require('query-string')

class Trees extends Component {
	
	constructor(props){
		super(props);
		this.onSelect = this.onSelect.bind(this)
		this.state = {
			data:{},
			config:[],
			loading:false,
			path:"",
			title:"",
			items:[],
			inputs:qs.parse(this.props.location.search),
			branches:[]
		}
	}	
	
	componentDidMount(){
		console.log(this.props.match.params)
		this.setState({loading:true},()=> apishka('get', {}, {path:this.props.match.params.path}, '/api/treesbypath', 
			(data) => {
				document.title = data.outjson.title	
				this.setState({
					loading:false, 
					data:data.outjson, 
					branches:data.outjson.branches, 
					items:data.outjson.items, 
					path:data.outjson.path, 
					title: data.outjson.title
				} ,()=>NotificationManager.success("OK")) 
			}, 
			(err) => this.setState({loading:false},() => {
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				} else {
					NotificationManager.error('unknown error!')
				}
												
			})
		))	
	}
	
	componentWillReceiveProps(n){
		console.log('N',n.location)
		this.setState({inputs:qs.parse(n.location.search)})
	}	
	
	onSelect(key) {
		let items = this.state.items
		items = items.filter((x) => x.key === key)[0]	
		
		if (items.path && items.treeviewtype>0) {
			console.log("ITEMS:",items)
			this.setState({path:items.path, viewtype:items.viewtype, treeviewtype:items.treeviewtype })
		}
	}
		
	render() {
		return (
			<div>
				<PreLoader on = {this.state.loading} />
				<h2>{this.state.data.title}</h2>
				<div style = {{margin:15}} className = "row">
					<div className = "col-md-4">
					
						<Tree value={this.state.branches} 
							selectionKeys={this.state.branches}
							selectionMode="single" 
							onSelectionChange={(e) => this.onSelect(e.value)} />
					</div>	
					<div className = "col-md-6">
							<div>
								{(this.state.treeviewtype === 1)? 
									<div>
										{(this.state.viewtype === 'table')?
											<List compo = {true} path = {this.state.path}  search = {this.props.location.search} 
												  inputs = {this.state.inputs} />
												:
											<div>
												{(this.state.viewtype === 'form full')?
													<GetOne compo = {true} search = {this.props.location.search}  path = {this.state.path} inputs = {this.state.inputs} />
													:
													null
												}
											</div>
										}
									</div>
									: <div>{(this.state.treeviewtype === 2)? 
										<Composition compo = {true} path = {this.state.path}  search = {this.props.location.search} 
												  inputs = {this.state.inputs} />
									: null}</div>		  
								}
							</div>	
					</div>	
				</div>	
				<NotificationContainer/>
			</div>	
		);
	}
}

export default Trees
